export default {
	data() {
		return {
			news: []
		};
	},
	onLoad: function() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				console.log(JSON.stringify(res));
				this.news = res.data;
				
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openNew(e) {
			console.log(JSON.stringify(e));
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				// url: '../new/new-info',
				url: '../info/info?newsid='+newsid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},

}
